import React from 'react'

import { Locale } from '~/lib/i18n/config'

import { ColorThemeSwitcher } from './colorThemeSwitcher'
import LanguageSwitcher from './langSwitcher'

export enum DropdownMenuTrigger {
  THEME = 'theme',
  LANGUAGE = 'language'
}
export type Drop = DropdownMenuTrigger | null
interface IThemeAndLanguage {
  locale: Locale
}

export const ThemeAndLanguage: React.FC<IThemeAndLanguage> = ({ locale }) => {
  const [openDropdown, setOpenDropdown] = React.useState<Drop>(null)
  return (
    <div className="flex items-center gap-4">
      <ColorThemeSwitcher dropdown={openDropdown} setOpenDropdown={setOpenDropdown} />
      <LanguageSwitcher locale={locale} dropdown={openDropdown} setOpenDropdown={setOpenDropdown} />
    </div>
  )
}
