'use client'

import { Moon, Sun } from 'lucide-react'
import { useTheme } from 'next-themes'
import * as React from 'react'
import { Dispatch, FC, SetStateAction } from 'react'
import { useTranslation } from 'react-i18next'
import DropdownMenuTrigger from './themeAndLanguage'
import { Button, DropdownMenu } from '~/components/ui'
import {
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger
} from '~/components/ui/dropdown-menu'

import { Drop } from './themeAndLanguage'

interface IColorThemeSwitcher {
  dropdown: Drop
  setOpenDropdown: Dispatch<SetStateAction<Drop>>
}
export const ColorThemeSwitcher: FC<IColorThemeSwitcher> = ({ dropdown, setOpenDropdown }) => {
  const { setTheme } = useTheme()
  const { t } = useTranslation('common')

  const themes = [
    { value: 'light', label: t('theme.light') },
    { value: 'dark', label: t('theme.dark') },
    { value: 'system', label: t('theme.system') }
  ]
  return (
    <DropdownMenu open={dropdown === DropdownMenuTrigger.THEME} onOpenChange={() => setOpenDropdown('theme')}>
      <DropdownMenuTrigger asChild>
        <Button variant="outline" size="icon">
          <Sun className="h-[1.2rem] w-[1.2rem] scale-100 rotate-0 transition-all dark:scale-0 dark:-rotate-90" />
          <Moon className="absolute h-[1.2rem] w-[1.2rem] scale-0 rotate-90 transition-all dark:scale-100 dark:rotate-0" />
          <span className="sr-only">Toggle theme</span>
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent onCloseAutoFocus={(e) => e.preventDefault()} align="end">
        {themes.map(({ value, label }) => (
          <DropdownMenuItem key={value} onClick={() => setTheme(value)}>
            {label}
          </DropdownMenuItem>
        ))}
      </DropdownMenuContent>
    </DropdownMenu>
  )
}
